.order-top{
    width: 280px;
    text-align: center;
    margin: 51px 0px;
    padding: 20px 0px;
    box-shadow: 0 0 0 10px rgba(241, 226, 226, 0.156),
    0 0 10px rgba(0, 0, 0, 0.18);
    position: fixed;
    top: 20px;
    left: 10px;
}
.order-block{
    width: 250px;
    margin: 0 auto;
    background: rgb(241, 235, 235);

}
.order-heading{
    font-size: 18px;
    text-transform: uppercase;
    font-weight: normal;
    color: rgb(95, 92, 92);
}
.order-list{
    padding: 5px;
}
.order-list ul{
    border-top: 2px solid gray;
}
.order-list li{
    padding: 15px 0px;
    /* border-top: 2px solid gray; */
    border-bottom: 2px solid gray;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: 16px;
}
.order-btn{
    margin: 0px 0px 0px px;
    font-size: 28px;
}