.login{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.login-nav{
    padding: 80px;
    margin: 70px;
}
.login-nav h2{
    font-size: 30px;
    font-weight: bold;
    margin: 30px;
}
.login-nav p{
    font-size: 22px;
    margin: 20px;
    color: rgb(71, 59, 59);
}
.login-nav button{
    padding: 15px 50px;
    font-size: 20px;
    text-transform: uppercase;
    color: #fff;
    background: red;
    border-radius: 5px;
}