.top {
  background: rgb(241, 235, 235);
  padding: 10px;
  margin: 10px;
  box-shadow: 0 0 0 10px rgba(241, 226, 226, 0.156),
    0 0 10px rgba(0, 0, 0, 0.18);
    position: relative;
}
.wrap {
  padding: 50px;
  background: url(./burger-logo-4.jpg) no-repeat;
  background-position: right;
  background-size: contain;
  position: relative;
}
.rating {
  display: inline-block;
  background: rgb(228, 228, 65);
  color: green;
  border-radius: 20px;
  padding: 7px 10px;
  font-size: 24px;
  margin: 0px 0px 10px 0px;
}
.star {
  color: #fff;
}
.header-heading {
  font-size: 54px;
  padding: 7px 0px;
  color: rgb(167, 70, 53);
  text-transform: uppercase;
}
.header-heading h1 {
  font-weight: bold;
  font-size: 44px;
  margin: 10px 0px;
}
.header-sub-heading {
  font-size: 32px;
  color: rgb(204, 83, 83);
  font-weight: 500;
}
.header-sub-heading span {
  color: red;
  font-weight: bold;
  margin: 0px 0px 5px 0px;
}
.header-btn{
  width: 25px;
  height: 25px;
}
.header-btn button{
  background: none;
}
.header-btn-basket{
  position: absolute;
  top: 25px;
  right: -12%;
}
.header-btn-admin{
  position: absolute;
  top: 5px;
  left: -15%;
}
