.edit-burger-form{
    border: 3px solid #731c1c;
    margin: 20px;
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    color: #000;
}
.edit-burger-form input, select{
    margin: 10px;
    width: 45%;
    padding: 10px;
    border: 1px solid rgb(193, 35, 35);
    border-radius: 5px;
    color: #000;
}

.edit-burger-form textarea{
    width: 100%;
    height: 100px;
    resize: none;
    padding: 10px;
    margin: 0px 10px 10px 5px;
    border-radius: 5px;
    border: 1px solid rgb(193, 35, 35);
    color: #000;
}
.edit-burger-form-textarea{
    padding: 10px;
text-align: center;
align-items: center;
}
