.burger-container {
  padding: 20px;
  background: rgb(255, 255, 255);
  display: flex;
  border-radius: 5px;
  margin: 20px 0px;
}
.burger-image-container img {
  width: 300px;
}
.burger-text {
  padding: 5px 15px;
  margin: 10px;
  position: relative;
}
.burger-text h3 {
  display: flex;
  justify-content: space-between;
  font-size: 36px;
  font-weight: bold;
  color: rgb(56, 43, 43);
  margin: 0px 0px 25px 0px;
}

.burger-text p {
  font-size: 24px;
  letter-spacing: 0.01em;
  margin: 0px 0px 25px 0px;
}

.burger-text button {
  padding: 10px;
  border-radius: 10px;
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  color: #fff;
  background: rgb(213, 44, 44);
}
.burger-text button:disabled {
  position: absolute;
  top: 55px;
  background: rgb(255, 255, 255);
  border: 2px solid gray;
  color: red;
  left: 140px;
  transform: rotate(-15deg);
  transition: 1s;
  font-size: 46px;
  padding: 25px;
}
