.menu-admin-top{
    margin: 10px 0px;
    padding: 20px;
    text-align: center;
    box-shadow: 0 0 0 10px rgba(241, 226, 226, 0.156),
    0 0 10px rgba(0, 0, 0, 0.18);
}
.menu-admin-block{

    width: 550px;
    margin: 0 auto;
    background: rgb(241, 235, 235);

}
.menu-admin-heading{
    font-size: 42px;
    text-transform: uppercase;
    font-weight: normal;
    color: rgb(95, 92, 92);
}
.menu-admin-btn{
    padding: 10px;
    background: red;
    border-radius: 10px;
    color: #fff;
    margin: 10px 0px 20px 0px;
}
.menu-admin-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 50px;
}
.menu-admin-avatar img{
    width: 100px;
border-radius: 50%;
}
.menu-admin-btn-logout{
    background: red;
    color: #fff;
    text-transform: uppercase;
    font-size: 18px;
    border-radius: 10px;
    padding: 10px;
    height: 20%;
}