.add-burger-form{
    border: 3px solid #b09999;
    margin: 120px 0px 20px 0px;
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    padding: 20px;
}
.add-burger-form input, select{
    margin: 10px;
    width: 45%;
    padding: 10px;
    border: 1px solid rgb(213, 192, 192);
    border-radius: 5px;
    color: gray;
}

.add-burger-form textarea{
    width: 85%;
    height: 15%;
    resize: none;
    padding: 10px;
    margin: 0px 10px 10px 5px;
    border-radius: 5px;
}
.add-burger-form-textarea{
    display: flex;
    padding: 10px;
text-align: center;
align-items: center;
}
.add-burger-form-textarea button{
    border: 2px solid gray;
    padding: 5px;
    border-radius: 25px;
    color: rgb(105, 37, 37);
}