.restaurant_select {
  padding: 10px;
  padding: 100px 10px;
  margin: 100px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.1), 0 0 10px rgba(0, 0, 0, 0.18);
}
.restaurant_select_top {
  width: 450px;
  padding: 10px;
  font-size: 30px;
  text-transform: uppercase;
  position: relative;
}
.restaurant_select_top-header {
  margin: 0px 35px 0px 0px;
}
.arrow_picker {
  position: absolute;
  top: 0px;
  right: 0px;
}
.arrow_picker-up {
  background: url(./up.svg) no-repeat;
  background-size: 100% auto;
  height: 13px;
  width: 13px;
  margin: 5px 0px;
}
.arrow_picker-down {
  background: url(./down.svg) no-repeat;
  background-size: 100% auto;
  height: 13px;
  width: 13px;
  margin: 5px 0px;
}
.restaurant_select_bottom {
  font-size: 20px;
  font-weight: 500;
  width: 400px;
  border: solid rgba(255, 0, 0, 0.619);
  box-shadow: rgb(255, 0, 0);
  padding: 10px 20px;
  position: absolute;
  top: 280px;
}
.restaurant_select_bottom li {
  margin: 10px 0px;
  cursor: pointer;
}
.restaurant_select_bottom li:hover {
  transform: scale(1.05);
  transition: 0.5s;
  color: red;
}
.restaurant_select button {
  padding: 10px;
  margin: 10px 0px;
  background: red;
  color: #fff;
  border-radius: 2px;
  text-transform: uppercase;
  cursor: pointer;
}